import React from 'react';

import { PrjTopCmp, PrjInfoCmp, PrjBottomCmp } from '../component';

import '../css/project.scss';

import projectData from './projectData.json';

// image import

import prjImg1 from '../images/work/work01_img_01.png';
import prjImg2 from '../images/work/work01_img_02.png';
import prjImg3 from '../images/work/work01_img_03.png';

class Project1 extends React.Component {
    constructor(props){
        super(props);

        // json 파일에서 가져올 리스트 번호
        this.idx = 1;
        this.state = {
            type: projectData[this.idx].type,        //project type
            cate: projectData[this.idx].cate,        //sorting category / 카테고리의 클래스를 받아 sorting
            title: projectData[this.idx].title,      //title
            desc: projectData[this.idx].desc,        //description
            mission: projectData[this.idx].mission,  //mission
            partner: projectData[this.idx].partner,  //partner
            launch: projectData[this.idx].launch,    //launch
            url: projectData[this.idx].url,          //url
        }
    }

    render(){
        return (
            <React.Fragment>

                <PrjTopCmp
                    type={this.state.type} 
                    title={this.state.title} 
                    desc={this.state.desc} 
                />

              

                <div className="content">

                    <PrjInfoCmp 
                        mission={this.state.mission}
                        partner={this.state.partner}
                        launch={this.state.launch}
                        url={this.state.url}
                    />

                    <div className="view_coltype">
                        <div className="left">
                            <p className="tit">introduce</p>
                        </div>
                        <div className="right">
                            <p className="desc">
                                An interactive visual dashboard that displays your information on one screen
                                Composed of content related to you, such as your favorite music, photos, stories, projects.
                            </p>

                        </div>
                    </div>
                </div>

                
                <div className="img_wrap">
                    <img src={prjImg1} alt=""/>
                </div>


                <div className="content">
                    <div className="view_coltype">
                        <div className="left">
                            <p className="tit">Project<br/>Background</p>
                        </div>
                        <div className="right">
                            <p className="desc">
                            The personal homepage service is composed of a dashboard type interface that displays my information and favorite elements on one screen.
                            </p>
                           
                        </div>
                    </div>
                </div>

                <div className="img_wrap">
                    <img src={prjImg2} alt=""/>
                </div>

                
                <div className="content">
                    <div className="view_coltype">
                        <div className="left">
                            <p className="tit">Platform</p>
                        </div>
                        <div className="right">
                            <p className="desc">
                                An interactive visual dashboard that displays your information on one screen <br/>Composed of content related to you, such as your favorite music, photos, stories, projects
                            </p>
                        </div>
                    </div>
                </div>


                <div className="img_wrap">
                    <img src={prjImg3} alt=""/>
                </div>   

                
                
                <div className="content">
                    <div className="view_coltype">
                        <div className="left">
                            <p className="tit">Features</p>
                        </div>
                        <div className="right">
                            <p className="desc">
                            Personalized platform for customizing color and elements <br/>- Monitor information at a glance using visual elements
                            </p>
                        </div>
                    </div>

                    <div className="view_coltype">
                        <div className="left">
                            <p className="tit">UI/UX</p>
                        </div>
                        <div className="right">
                            <p className="desc">
                            Provides intuitive information using minimal elements and data, and provides an experience that seems like viewing gallery works through interaction elements
                            </p>
                        </div>
                    </div>
                </div>

                <PrjBottomCmp/>

            </React.Fragment>
        )
    }
}

export default Project1;